<template>

  <header class="header">
    <img src="./assets/logo.png" alt="logo" class="logo">
    <button v-if="step != 0"  class="header-button-left" @click="step=0">Cancel</button>
    <button v-if="step == 1" class="header-button-right" @click="step=2">Next</button>
    <button v-if="step == 2" class="header-button-right" @click="publish">글등록</button>
  </header>

  <!-- 변수에 고정된 내용이 변경되어야 실행됨 -->
  <p>computed : {{ 날짜계산 }} count:{{count}}</p>
  <!-- methods 누를때마다 재실행 -->
  <p>methods : {{ 날짜함수() }} count:{{count}}</p>
  <button @click="count++">업데이트</button>

  <p>{{ 서버자료 }}</p>
  <p>{{ 이름 }}</p>
  <p>{{ 나이 }}</p>
  <p>---------------------------------------------------------</p>
  <p>{{ myName }}</p>
  <p>{{ myAge }}</p>
  <p>{{ myData }}</p>
  <button @click="$store.commit('이름변경','김수한무')">이름변경하자구요~</button>
  <button @click="이름변경('박길동')">이름변경하자구요~2</button>
  <button @click="$store.dispatch('외부자료호출')">외부자료호출</button>
  <button @click="외부자료호출()">외부자료호출(m-action))</button>


  <!-- :게시물(Container.vue쪽) = "게시물"(data()쪽) -->
  <Container @write="작성글=$event" :게시물="게시물" :step="step" :업로드이미지="업로드이미지" />
  <button v-if="step == 0" @click="moreData">게시물 더보기</button>


  <footer class="footer">
    <div v-if="step == 0" class="footer-button-plus">
      <!-- (아래)파일 받아오는 코드 -->
      <!-- multiple : 다중선택, accept="image/*" : 업로드 파일 종류 이미지 범위로 선택됨 -->
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile">
      <label for="file" class="input-plus"> + </label>
    </div>
  </footer>
  
    

</template>

<script>
import postdata from './assets/postdata'
import Container from './components/Container.vue'

import axios from 'axios'
import { mapState, mapMutations, mapActions } from 'vuex'

  export default {
    name: 'App',
    data() {
      return {
        게시물: postdata,
        step: 0,
        업로드이미지 : '',
        작성글 : '',
        저장할필터: '',
        count: 0
      }
    },

    mounted(){
      this.emitter.on('필터적용신호', (a)=>{
        this.저장할필터 = a;
      });
    },

    computed: { 
          //computed는 리턴이 반드시 필요, 매개변수를 전달할수 없다.
          //사용시 변수처럼 입력하여 사용한다.
          날짜계산(){
            return new Date();
          },
          ...mapState(['이름','나이', '서버자료']),
          ...mapState({
            myName: '이름',
            myAge: '나이',
            myData: '서버자료'
          })
          
          
    },

    methods: {
      ...mapMutations(['이름변경', '추가', '좋아요']),
      ...mapActions(['외부자료호출']),

      날짜함수(){
        return new Date();
      },

      moreData(){
        // 경로문제가 발생할 수 있기에 then 아래 구문에서 function 대신 화살표함수 사용
        axios.get(`https://tarolong.github.io/more0.json`).then((res)=>{
          // console.log(res.data);
          this.게시물.push(res.data);
          // 데이터의 유실이 우려되는 경우, 위 구문이 아닌. try catch 구문을 이용하여 작성해보자.(작성안됨)
        });
      },

      upload(event){
        // console.log(event.target.files);
        let temp = event.target.files[0]
        this.step = 1;
        let url = URL.createObjectURL(temp);
        this.업로드이미지 = url;
      },

      publish(){
        let 새거 = {
          name: "Kim Hyun",
          userImage: "https://placeimg.com/100/100/arch",
          postImage: this.업로드이미지,
          likes: 36,
          date: "May 15",
          liked: false,
          content: this.작성글,
          filter: this.저장할필터
        }
        // push()는 뒷자리에 추가됨. 앞자리 추가는 'unshift()' 사용
        this.게시물.unshift(새거);
        this.step = 0;

      }

    },
    components: {
      Container
    }
    
  }
</script>

