<template>
    <div @click="fire" class="filter-item" :class="필터" :style="{backgroundImage:`url(${이미지})`}">
        <!-- <span>{{필터}}</span> -->
        <!-- 슬롯으로 위 {{필터}} 내용을 작성해보자 -->        
        <slot name="번호"></slot>
        <slot name="이름"></slot>
        <!-- <button >필터적용</button> -->
    </div>
    
</template>

<script>
export default {
    name: 'FilterBox',
    props: {
        이미지:String,
        필터:String
    },
    methods: {
        fire(){
            this.emitter.emit('필터적용신호', this.필터)
        }
    }
}
</script>

<style>
    .filter-item{
        position: relative;
        display: inline-block;
        width: 100px;
        height: 100px;
        margin: 10px 10px 10px auto;
        padding: 10px;
        background: center;
        background-size: cover;
    }
    .filter-item span{
        display: block;
        background: rgba(0,0,0,0.3);
        color: #fff;
        text-align: center;
    }
</style>